@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: 'Rubik', sans-serif;
}


.top-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(/ip-tracker/static/media/pattern-bg.8d3f4ad6.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    min-height: 40vh;
    width: 100%;
    height: 100%;
}

.title {
    color: white;
}

.search-container {
    display: flex;
    height: 50px;
    width: 40vw;
    justify-content: center;
    background-color: white;
    border-radius: 15px;
}

.search {
    width: 100%;
    padding: 10px;
    border: none;
    color: #969696;
    border-radius: 15px;
    font-size: 1.1rem;
    outline: none;
}

.icon-container {
    height: 50px;
    border: 1px solid black;
    background-color: black;
    width: 50px;
    border-radius: 0 15px 15px 0;
    display: flex;
    margin-left: auto;
    cursor: pointer;
}

.icon-container img {
    margin: auto;
}

@media (max-width: 900px) {
    .search-container {
        width: 90%;
    }

    .input-form {
        display: contents;
    }
}

.panel-container {
    background-color: white;
    display: flex;
    position: absolute;
    justify-content: space-around;
    top: 30vh;
    width: 60%;
    z-index: 5;
    box-shadow: 10px 10px 10px rgb(0, 0, 0, 0.5);
    border-radius: 10px;
    padding: 30px 0;
}

.section-container {
    border-left: 1px solid #969696;
    padding-left: 20px;
    width: 30vw;
}

.panel-container div:nth-child(1) {
    border-left: none;
}
.section-label {
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 1px;
    color: #969696;
}

.section-value {
    font-size: 24px;
    color: #2b2b2b;
}

@media (max-width: 900px) {
    .panel-container {
        flex-direction: column;
        width: 90%;
        justify-content: center;
        top: 25vh;
    }
    .section-container {
        width: 100%;
        text-align: center;
        border: none;
        padding: 0;
    }

    .section-value {
        font-size: 18px;
    }
}

#mapid {
    height: 600px;
    width: 100%;
    z-index: 0;
}

